import Signal from 'Wdgt/Signal';

const LIST = 'Proxy.SecuredWebsites.list';

class SecuredWebsites {
  constructor() {
    this.list = sdk.storage.get(LIST) ? sdk.storage.get(LIST) : undefined;
    if(!this.list) {
      this.list = {};

      sdk.storage.set(LIST, this.list);
    }
    this.onChange = new Signal();
    this.lock = {};
    sdk.tabs.onPageView.add((info) => {
      if(info && info.domain && this.isInList(info.domain)) {
        this.list[info.domain].visited ++;
        sdk.storage.set(LIST, this.list);
      }
    });
  }

  add(domain, lock) {
    return new Promise((resolve, reject) => {
      if(!this.list[domain]) {
        this.lock[domain] = true;
        this.list[domain] = {location: 'optimal', type: 'custom', visited: 0};
        sdk.storage.set(LIST, this.list);
        sdk.proxy.render();
        this.onChange.dispatch(this.get());
        return resolve(this.list);
      }

      return resolve(this.list);
    });
  }

  get() {
    return this.list;
  }

  remove(domain) {
    return new Promise((resolve, reject) => {
      if(this.list[domain]) {
        delete this.list[domain];
        return sdk.storage.set(LIST, this.list).then(() => {
          sdk.proxy.render();
          this.onChange.dispatch(this.get());
          return resolve();
        });
      }

      return resolve();
    });
  }

  isInList(domain) {
    return !!this.list[domain];
  }

  isLocked(domain) {
    return !!(this.lock[domain]);
  }

  unlock(domain) {
    delete this.lock[domain];
  }
}

export default SecuredWebsites;