var proto = {};

proto.content  = `package proto.api.request;
message DeviceInfo {
	enum Platform {
		IOS              = 1;
		ANDROID          = 2;
		WINDOWS          = 3;
		MAC              = 4;
		EXTENSION_CHROME = 5;
		OPEN_WRT         = 6;
		EXTENSION_FFOX   = 7;
	}

	required Platform platform           = 1;
	required string   hash               = 2;
	required string   package_name       = 3; // ex: com.example.myapp
	required int32    app_version        = 4;
	required string   model              = 5; // ex: iPhone 6/Galaxy S3
	required string   make               = 6; // ex: Samsung/Apple/HP/etc
	required string   os_name            = 7; // ex: Windows 7 build 12324
	required string   language           = 8;
	optional string   signature          = 9;
	optional string   token              = 10; // authorization token. If user not authorized, must be left empty
	optional string   channel            = 11; // installation channel
	optional string   bn_proxy_device_id = 12;
	//optional string flavour
}

message Config {
	required DeviceInfo DeviceInfo = 1;
}

message SD {
	required DeviceInfo DeviceInfo = 1;
	required string requested_country = 2;
  optional string additional_data   = 3;
}

message RestorePassword {
	required DeviceInfo DeviceInfo = 1;
	required string     email      = 2;
}

message Signin {
	required DeviceInfo DeviceInfo = 1;
	required string     login      = 2;
	required string     password   = 3;
}

message Signout {
	required DeviceInfo DeviceInfo = 1;
}

message Signup {
	required DeviceInfo DeviceInfo = 1;
	required string     email      = 2;
	required string     password   = 3;
}

message Status {
	required DeviceInfo DeviceInfo = 1;
}

message BNLink {
    required DeviceInfo DeviceInfo = 1;
    required string     email      = 2;
}`;

export default proto;