import Signal from 'Wdgt/Signal';

import Core  from './Core';

const STATUS  = 'Proxy.Status.status';
const DESKTOP = 'Proxy.Status.desktopApplication';

const ERROR_LIMIT           = 50;
const ON_PROXY_ERROR_RECALL = 5 * 1000;

class Status extends Core {
  constructor() {
    super();

    this.status = sdk.storage.get(STATUS) ? sdk.storage.get(STATUS) : {status: 'disconnected', location: 'optimal'};

    this.createSignals();
    this.watchForProxySettings();
    this.watchForProxyError();
  }

  createSignals() {
    this.onProxyError          = new Signal();
    this.onStatusChanged       = new Signal();
    this.onGlobalStatusChanged = new Signal();
  }

  updateStatus(status, location) {
    this.status.status   = status;
    this.status.location = (location?location:this.status.location);

    return sdk.storage.set(STATUS, this.status);
  }

  watchForProxySettings() {
    let proxySettings = (details) => {
      if(!details) return;

      let desktopApplication = sdk.storage.get(DESKTOP);

      if(desktopApplication) {
        this.updateGlobalStatus('desktop')
      }
      else if ((details.levelOfControl === 'not_controllable') || (details.levelOfControl === 'controlled_by_other_extensions')) {
        this.updateGlobalStatus('unavailable');
      }
      else {
        this.updateGlobalStatus('avaliable');
      }
    };

    browser.proxy.settings.get({}, proxySettings.bind(this));
    browser.proxy.settings.onChange.addListener(proxySettings.bind(this));
  }

  updateGlobalStatus(status) {
    if(this.globalStatus === status) return;
    this.globalStatus = status;
    this.onGlobalStatusChanged.dispatch(status);
  }

  getGlobalStatus() {
    return this.globalStatus;
  }

  watchForProxyError() {
    let lastError;
    let errorCounter  = 0;
    let waitForRecall = false;

    browser.proxy.onProxyError.addListener((info) => {
      if(sdk.proxy.disableErrors || waitForRecall) return;

      waitForRecall = true;

      errorCounter = (lastError&&(lastError<(Date.now()-30*1000)))?0:++errorCounter;
      lastError    = Date.now();

      if(errorCounter===ERROR_LIMIT) {
        return sdk.proxy.disconnect().then(() => {
          this.onProxyError.dispatch('disconnect');
          errorCounter  = 0;
          waitForRecall = false;
        });
      }

      this.onProxyError.dispatch('update');
      this.render(true);

      setTimeout(() => {
        waitForRecall = false;
        this.render();
      }, ON_PROXY_ERROR_RECALL);
    });
  }

  disableOtherProxy() {
    let enableExtention = (id, enable) => {
      return new Promise((resolve, reject) => {
        browser.management.setEnabled(id, false, () => {
          resolve();
        });
      });
    }

    let retrieveExtensionInfos = () => {
      return new Promise((resolve, reject) => {
        browser.management.getAll((extensionInfos) => {
          resolve(extensionInfos);
        });
      });
    }

    return retrieveExtensionInfos().then((extensionInfos) => {
      let extensionsIds = [];
      let currentExtensionId = sdk.config.application.applicationId;

      for (let k in extensionInfos) {
        let extensionInfo = extensionInfos[k];
        if (extensionInfo.id === currentExtensionId) {
          continue;
        }

        if (!extensionInfo.enabled) {
          continue;
        }

        if (extensionInfo.permissions.indexOf('proxy') === -1) {
          continue;
        }

        extensionsIds.push(extensionInfo.id);
      }

      return Promise.all(extensionsIds.map((extensionId) => {
        return enableExtention(extensionId, false);
      })).then(() => {
        return;
      });
    });
  }
}

export default Status;