import 'commonShared/SDK';
import 'config';
import Debug from 'Wdgt/Debug';

import Storage from 'Wdgt/Storage';
import User from 'commonShared/User'
import Tabs from 'Wdgt/Tabs';
import Proxy from 'common/Proxy';
import Reporting from 'commonShared/Reporting';
import Monitoring from 'commonShared/Monitoring';
import IconController from './IconController';
import DnsPrefetch from 'commonShared/DnsPrefetch';

class background {
  constructor() {
    this.init();
  }

  init() {
    new Debug();
    (new Storage()).init().then(() => {
      sdk.storage.set('Proxy.proxyLocal', true);
      new User().init();
      (new Reporting()).init().then(() => {
        (new Tabs()).init().then(() => {
          new Proxy();
          new Monitoring();
          new IconController();
          new DnsPrefetch();
          sdk.loading.loaded = true;
        });
      });

    });
  }
}

export default background;
