import Network     from 'Wdgt/Network';
import objectMerge from 'object-merge';

const URL = 'http://www.google-analytics.com/collect';
const PARAMETERS_MAP = {
  'version': 'v',
  'clientId': 'cid',
  'userId': 'uid',
  'trackingId': 'tid',
  'hitType': 't',
  'applicationName': 'an',
  'applicationId': 'aid',
  'applicationVersion': 'av',
  'contentDescription': 'cd',
  'category': 'ec',
  'action': 'ea',
  'label': 'el',
  'value': 'ev',
  'screenName': 'cd',
  'campaignName': 'cn',
  'campaignSource': 'cs',
  'campaignMedium': 'cm',
  'clientIpAddress': 'uip',
  'userAgent': 'ua',
};

const PARAMS = 'Reporting.Analytics.param';

class Analytics {
  init() {
    if(!sdk || !sdk.config || !sdk.config.reporting || !sdk.config.reporting.analytics || !sdk.config.reporting.analytics.id) return new Promise(resolve => {resolve();});

    return this.restoreParams();
  }

  event(params) {
    return new Promise((resolve) => {
      if(!params || !params.category || !sdk || !sdk.config || !sdk.config.reporting || !sdk.config.reporting.analytics || !sdk.config.reporting.analytics.id) return resolve();

      let config   = sdk.config.reporting.analytics;
      let rand     = Number(Date.now().toString().match(/[0-9]{2}$/));
      let sampling = config.sampling[params.category] ? config.sampling[params.category] : config.sampling.default;

      if(rand > (sampling-1)) return resolve();

      if(!config.noPrefix) {
        params.category = "_"+params.category;
      }
      params.hitType  = 'event';
      params.version  = '1';
      params.clientId = sdk.user.hash;
      params.applicationId      = sdk.config.application.id;
      params.applicationName    = sdk.config.application.name;
      params.applicationVersion = sdk.config.application.version;
      params = objectMerge(params, this.params);

      return resolve(this.send(params));
    });
  }

  send(params) {
    return new Promise((resolve, reject) => {
      let stringify = (parameters) => {
        let shortParameters = {};
        for (let name in parameters) {
          let value = parameters[name];
          if ((typeof(value) === 'undefined') || (value == null)) {
            continue;
          }

          shortParameters[PARAMETERS_MAP[name] || name] = value;
        }

        let bodyParts = [];
        for (let key in shortParameters) {
          bodyParts.push(key + '=' + encodeURIComponent(shortParameters[key]));
        }

        return bodyParts.join('&');
      };

      Network.post(URL, stringify(params)).then(() => {
        return resolve();
      }).catch(() => {
        return resolve();
      });
    });
  }

  restoreParams() {
    this.params = sdk.storage.get(PARAMS) ? sdk.storage.get(PARAMS) : {};

    return new Promise((resolve) => {
      if(!this.params.trackingId) {
        return this.createParams().then(() => {
          return resolve();
        });
      }
      return resolve();
    });
  }

  createParams() {
    let params = {
      trackingId: sdk.config.reporting.analytics.id
    };

    return new Promise((resolve) => {
      this.updateParams(params).then(() => {
        browser.tabs.query({
          url: `https://chrome.google.com/*${sdk.config.application.applicationId}*`,
        }, (tabs) => {
          var tab = tabs[0];
          if (!tab) {
            return resolve();
          }

          var campaignName   = tab.url.match(/utm_campaign=([^&]+)/);
          var campaignSource = tab.url.match(/utm_source=([^&]+)/);
          var campaignMedium = tab.url.match(/utm_medium=([^&]+)/);

          var params = {
            campaignName:   (campaignName&&campaignName[1])     ? campaignName[1]   : null,
            campaignSource: (campaignSource&&campaignSource[1]) ? campaignSource[1] : null,
            campaignMedium: (campaignMedium&&campaignMedium[1]) ? campaignMedium[1] : null
          };

          this.updateParams(params).then(() => {
            return resolve();
          });
        });
      });
    });
  }

  updateParams(params) {
    this.params = objectMerge(this.params, params);

    return sdk.storage.set(PARAMS, this.params);
  }
}

export default Analytics;