import Network from 'Wdgt/Network';

const URL      = 'https://event.shelljacket.us/api/report/chrome_ext';
const QUEUE    = 'Reporting.Internal.queue';
const INTERVAL = 2.5 * 60 * 1000;
const GDPR_CONNECTED = 'GDPR.Connected';

class Internal {
  constructor() {
    this.queue = sdk.storage.get(QUEUE) ? sdk.storage.get(QUEUE) : [];

    this.send();
    setInterval(this.send.bind(this), INTERVAL);
  }

  event(params) {
    params.payload.version = sdk.config.application.dotVersion;
    params.payload.country = sdk.user.country;
    params.payload.hash    = sdk.user.hash;
    params.payload.id      = sdk.config.application.id;
    params.payload.ts      = Date.now();

    this.queue.push(params);

    return sdk.storage.set(QUEUE, this.queue).then(() => {
      return params;
    })
  }

  send() {
    if(this.queue.length === 0 || !sdk.storage.get(GDPR_CONNECTED)) return;

    let temp   = this.queue;
    this.queue = [];

    let count  = this.queue.length-1;
    let report = '';

    for(let k in temp) {
      report += JSON.stringify(temp[k]).toLowerCase();
      if(k !== count) report += "\n";
    }

    Network.post(URL, report).then(() => {
      sdk.storage.set(QUEUE, this.queue);
    }).catch((e) => {
      this.queue = this.queue.concat(temp);
      sdk.storage.set(QUEUE, this.queue);
    });
  }
}

export default Internal;