import { detect } from 'detect-browser'
const browser = detect();

export const browserName = browser && browser.name;
export const browserVersion = browser && browser.version;
export const browserOS = browser && browser.os;

export const isChrome = browserName === 'chrome';
export const isFirefox = browserName === 'firefox';
export const isEdge = browserName === 'edge-chromium' || browserName === 'edge'
