import Signal from 'Wdgt/Signal';

const LIST = 'Proxy.BypassWebsites.list';

class BypassWebsites {
  constructor() {
    this.list     = sdk.storage.get(LIST) ? sdk.storage.get(LIST) : [];
    this.onChange = new Signal();
  }

  add(domain) {
    return new Promise((resolve, reject) => {
      if(this.list.indexOf(domain) === -1) {
        this.list.push(domain);
        return sdk.storage.set(LIST, this.list).then(() => {
          sdk.proxy.render();
          this.onChange.dispatch(this.get());
          return resolve(this.list);
        });
      }

      return resolve(this.list);
    });
  }

  get() {
    return this.list;
  }

  remove(domain) {
    return new Promise((resolve, reject) => {
      if(this.list.indexOf(domain) !== -1) {
        this.list.splice(this.list.indexOf(domain), 1);
        return sdk.storage.set(LIST, this.list).then(() => {
          sdk.proxy.render();
          this.onChange.dispatch(this.get());
          return resolve();
        });
      }

      return resolve();
    });
  }

  isInList(domain) {
    if(this.list.indexOf(domain) !== -1) return true;
    return false;
  }
}

export default BypassWebsites;