import Network from 'Wdgt/Network';
import { browserVersion, isChrome, isEdge } from "../../BrowserDetection";

const FIREBASE_DATA = 'User.Authorization.firebaseData';

const DATA = 'User.data';

const URLS = {
  prod: [
    'api-prod-partner-us-west-2.northghost.com'
  ],
  debug: [
    'api-stage-partner-us-west-2.northghost.com'
  ],
};

class Authorization {
  constructor() {
    this.url;
  }

  init() {
    this.onBeforeSendHeaders();

    sdk.authorization = {
      getToken: this.getToken.bind(this)
    };

    return new Promise(resolve => {
      this.getToken().then(() => {
        if(sdk.config.debug) {
          this.getToken('debug').then(() => {
            return resolve();
          });
        } else {
          return resolve();
        }
      });
    });
  }

  getToken(type, i) {
    i = i===undefined?0:++i;
    type = type ? type : 'prod';
    return new Promise((resolve) => {
      if(URLS[type].length <= i || sdk.user.credentials[type].username) return resolve();

      let firebase = sdk.storage.get(FIREBASE_DATA);
      let url = 'https://'+URLS[type][i]+'/user/login'
        + '?device_id='+sdk.user.hash
        + '&device_name=Chrome%20Extension'
        + '&device_type='+sdk.config.authorization.platform+'_extension'
        + '&carrier_id='+sdk.config.authorization.carrier
        + '&tz='+(new Date().getTimezoneOffset())
        + '&auth_method='+(firebase?'firebase':'anonymous');

      if(firebase) {
        url += '&access_token='+firebase.token;
      }

      this.url = URLS[type][i];
      Network.post(url).then((response) => {
        this.url = undefined;
        try {
          response = JSON.parse(response);
        }catch (e) {
          response = undefined;
        }

        if(!response || !response.result || response.result !== 'OK') {
          return resolve(this.getToken(type, i));
        }

        try {
          sdk.user.credentials[type] = {
            id: response.subscriber.id,
            name: response.subscriber.name,
            carrier_id: response.subscriber.carrier_id,
            username: 'access_token',
            password: response.access_token,
          };
        } catch (e) {
          sdk.user.credentials[type] = {};
        }

        sdk.storage.set(DATA, sdk.user).then(() => {
          return resolve();
        });

      }).catch((e) => {
        this.url = undefined;
        resolve(this.getToken(type, i));
      });
    });
  }

  onBeforeSendHeaders() {
    const options = ["blocking", "requestHeaders"];
    /*
    https://developer.chrome.com/extensions/webRequest

    Starting from Chrome 72, if you need to modify responses before Cross Origin Read Blocking (CORB) can block the response, you need to specify 'extraHeaders' in opt_extraInfpSpec.

    Starting from Chrome 79, the following request header is not provided and cannot be modified or removed without specifying 'extraHeaders' in opt_extraInfoSpec:
    - Origin
    */
    if ((isChrome || isEdge) && (browserVersion >= "72")) {
      options.push("extraHeaders")
    }
    browser.webRequest.onBeforeSendHeaders.addListener((details) => {
      if(!this.url) return;

      if(details.url.indexOf(this.url) === -1) return;

      for(let k in details.requestHeaders) {
        if(details.requestHeaders[k].name.toLowerCase() === 'origin') {
          details.requestHeaders[k].value = 'https://'+this.url;
          break;
        }
      }

      return {requestHeaders: details.requestHeaders};
    }, {urls: ["https://*/*"]}, options);
  }
}

export default Authorization;
