import Config from './Config';

const ACTIVE = 'Proxy.Servers.active';
const LIMIT  = 5;

class Servers {
  constructor() {
    this.config = new Config();

    this.active = sdk.storage.get(ACTIVE) ? sdk.storage.get(ACTIVE) : [];
  }

  setActive(active) {
    this.active = active;
    return sdk.storage.set(ACTIVE, active)
  }

  getActive() {
    return this.active;
  }

  get(location, type) {
    if(!location || location === 'optimal') location = this.getOptimalLocation(type);

    return this.getServers(location, type);
  }

  getServers(location, type) {
    let servers = this.getServersByPriority((this.config.getServers(type))[location]).splice(0, LIMIT);
    if(servers.length < LIMIT) {
      let backup = this.getBackupServers(location, type);

      let i = 0;
      while (servers.length < LIMIT) {
        servers.push(backup[i]);
        i++;
      }
    }

    return servers;
  }

  getBackupServers(location, type) {
    let temp = this.config.getServers(type);
    let servers = [];

    for(let k in temp) {
      if(k === location) continue;

      for(let i in temp[k]) {
        servers.push(temp[k][i]);
      }
    }

    return this.getServersByPriority(servers);
  }

  getServersByPriority(servers, result) {
    let priorities = new Array();
    let total      = 0;
    result = result ? result : [];

    for(let k in servers) {
      total += servers[k].priority;
      priorities.push(total);
    }

    let rand = Math.floor(Math.random() * total);

    for(let i = 0; i < priorities.length; i ++) {
      if(rand <= priorities[i]) {
        result.push(servers[i]);
        servers.splice(i, 1);
        return this.getServersByPriority(servers, result);
      }
    }

    return result;
  }

  getOptimalLocation(type) {
    // @todo choose server based on network speed
    let locations = this.config.getLocations(type);
    return locations[[Math.floor(Math.random()*locations.length)]];
  }

  concat(servers) {
    let row = "";

    for(let k in servers) {
      if(!servers[k]) continue;
      let scheme = (servers[k].scheme === 'http') ? 'proxy' : servers[k].scheme;
      row += scheme + ' ' + servers[k].address + ':' + servers[k].port+';';
    }

    return row;
  }
}

export default Servers;