import Servers from './Servers';
import whiteListDomains from 'shared/config/whiteListDomains';

const PROXY_LOCAL = 'Proxy.proxyLocal';

class Core {
  constructor() {
    this.servers = new Servers();
    this.secured = undefined;
  }

  updateProxy() {
    let servers = this.servers.get(this.status.location, (sdk.user.status.elite?'elite':'regular'));
    return this.servers.setActive(servers).then(() => {
      return this.render();
    });
  }

  renderClearProxy() {
    let pacScript =
      "function FindProxyForURL(url, host) {\n"+
      "  return 'DIRECT';\n"+
      "}";

    return this.setPacScript(pacScript);
  }

  render(direct) {
    let proxyLocal = sdk.storage.get(PROXY_LOCAL), dr = '';

    if(proxyLocal && sdk.proxy.status.status === 'connected') {
      dr = "  var ip = dnsResolve(host);\n"+
           "  if(isPlainHostName(host) || shExpMatch(host, '*.local') || isInNet(ip, '10.0.0.0', '255.0.0.0') || isInNet(ip, '172.16.0.0',  '255.240.0.0') || isInNet(ip, '192.168.0.0',  '255.255.0.0') || isInNet(ip, '173.37.0.0',  '255.255.0.0') || isInNet(ip, '127.0.0.0', '255.255.255.0')) return 'DIRECT';\n\n";
    }

    let pacScript =
      "let active  = false,\n"+
      "    created = "+Date.now()+",\n"+
      "    started = Date.now();\n\n"+

      "if((started-100) < created) {\n"+
      "  active = true;\n"+
      "}\n\n"+

      "function FindProxyForURL(url, host) {\n"+
      "  if(!url.match(/^(https?)|(ftp):\\/\\//)) return 'DIRECT';\n"+
      "  if(!active && (Date.now() > (started + 2000))) active = true;\n"+
      "  if(!active) return 'DIRECT';\n\n"+
      this.excludedDomain()+
      dr+
      "  if(";

      for(let k in whiteListDomains) {
        pacScript += "shExpMatch(host, '"+whiteListDomains[k]+"') || ";
      }

      pacScript +=
        "shExpMatch(host, 'www.google-analytics.com') || shExpMatch(host, 'localhost') || shExpMatch(host, '127.0.0.1')) return 'DIRECT';\n\n"+
        this.bypassWebsitesRow()+
        this.globalRow(direct) +
        this.securedWebsitesRow(direct)+

      "  return 'DIRECT';\n"+
      "}";

    return this.setPacScript(pacScript);
  }

  excludedDomain() {
    return '';
  }

  globalRow(direct) {
    if(this.status.status !== 'connected' || this.globalStatus !== 'avaliable') return '';
    return "  return '"+(this.servers.concat( this.servers.getActive() ) + (direct?'DIRECT;':''))+"';\n";
  }

  bypassWebsitesRow() {
    if(!sdk.proxy || !sdk.proxy.bypass) return '';
    if(sdk.proxy.bypass.get().length === 0) return '';

    return "  if(/"+sdk.proxy.bypass.get().join('|').replace(/([-+.])/g, '\\$1')+"/.test(host)) return 'DIRECT';\n\n";
  }

  securedWebsitesRow(direct) {
    if(!sdk.proxy || !sdk.proxy.secured) return '';
    let securedWebsites = sdk.proxy.secured.get();
    if(Object.keys(securedWebsites).length === 0) return '';

    let a = [];
    for(let k in securedWebsites) {
      a.push(k);
    }

    if(!this.secured) {
      this.secured = this.servers.concat( this.servers.get('optimal', (sdk.user.status.elite?'elite':'regular')) );
    }

    return ("  if("+JSON.stringify(a)+".indexOf(host.replace(/^www\\./, '')) !== -1) return '"+this.secured+(direct?'DIRECT;':'')+"';\n");
  }

  setPacScript(pacScript) {
    return new Promise((resolve) => {
      debug(pacScript);
      debug('-----------------------------');

      let config = {
        mode: "pac_script",
        pacScript: {
          data: pacScript
        }
      };

      browser.proxy.settings.set(
        {value: config, scope: 'regular'},
        () => {
          resolve();
        }
      );

      browser.extension.isAllowedIncognitoAccess((result) => {
        if(!result) return;

        try {
          browser.proxy.settings.set(
            {value: config, scope: 'incognito_persistent'},
            () => {}
          );
        }catch(e) {
          error(e);
        }
      });
    });
  }
}

export default Core;