class Signal {
  constructor() {
    this.callbacks = [];
  }

  add(callback) {
    if(typeof (callback) !== 'function') return false;

    this.callbacks.push(callback);

    let obj = {
      detach: this.detach.bind(this, (this.callbacks.length-1))
    };

    return obj;
  }

  dispatch(params) {
    for(let k in this.callbacks) {
      try {
        this.callbacks[k](params);
      }
      catch (e) {}
    }

    return true;
  }

  detach(id) {
    this.callbacks.splice(id, 1);
    return true;
  }
}

export default Signal;