import Internal  from './Internal';
import Analytics from './Analytics';

class Reporting {
  init() {
    this.internalObj  = new Internal();
    this.analyticsObj = new Analytics();

    return this.analyticsObj.init().then(() => {
      sdk.reporting = {
        report: this.report.bind(this),
        internal: this.internal.bind(this),
        analytics: this.analytics.bind(this)
      };
    });
  }

  report(...params) {
    this.internal.apply(this, params);
    return this.analytics.apply(this, params);
  }

  internal(...params) {
    if(params.length === 0) {
      return new Promise((resolve) => {resolve();});
    }

    let fields = ['event', 'category', 'action', 'label', 'value'];
    let record = {};

    for(let k in params) {
      if(typeof(params[k]) !== 'object' && fields[k]) {
        record[fields[k]] = params[k];
        continue;
      }

      if(typeof(params[k]) === 'object') {
        for(let j in params[k]) {
          if(!params[k][j]) continue;
          record[j] = params[k][j];
        }
      }
    }

    if(!record.event) {
      return new Promise((resolve) => {resolve();});
    }
    let report = {
      event: record.event,
      payload: {}
    };

    delete record.event;
    for(let k in record) {
      report.payload[k] = record[k];
    }

    return this.internalObj.event(report);
  }

  analytics(...params) {
    if(params.length === 0) {
      return new Promise((resolve) => {resolve();});
    }
    let fields = ['category', 'action', 'label', 'value'];
    let record = {};

    for(let k in params) {
      if(typeof(params[k]) !== 'object' && fields[k]) {
        record[fields[k]] = params[k];
        continue;
      }

      if(typeof(params[k]) === 'object') {
        for(let j in params[k]) {
          record[j] = params[k][j];
        }
      }
    }

    if(!record.category) {
      return new Promise((resolve) => {resolve();});
    }

    if(record.value) {
      record.label = record.label + '_' + record.value;
    }

    return this.analyticsObj.event({
      category: record.category,
      action:   record.action,
      label:    record.label
    });
  }
}

export default Reporting;