import Network from 'Wdgt/Network';
import Elite from './Elite';
import Authorization from './Authorization';

const DATA = 'User.data';
const USER_HASH_KEY = 'User.hash';

class User {
  constructor() {
    this.data = sdk.storage.get(DATA);
    const userHash = sdk.storage.get(USER_HASH_KEY) || (this.data && this.data.hash) || '';

    if (!this.data || !this.data.credentials || !this.data.credentials.prod || !this.data.credentials.old) {
      this.data = {
        status: {},
        credentials: {
          prod: {},
          debug: {},
          old: {}
        }
      };
    }

    // Integration with old hash logic
    this.data.hash = userHash;
    this.hash();
    if (userHash !== this.data.hash) {
      sdk.storage.set(USER_HASH_KEY, this.data.hash);
    }

    this.percent();
    this.language();
    this.country();

    sdk.user = this.data;

    if (!sdk.user.credentials.old.username) {
      sdk.user.credentials.old.username = sdk.user.hash + ".h783ohaw09jdf0";
      sdk.user.credentials.old.password = sdk.user.hash + ".h78239hd";
    }
  }

  init() {
    return new Promise(resolve => {
      (new Elite()).init().then(() => {
        (new Authorization()).init().then(() => {
          return resolve();
        });
      });
    });
  }

  hash() {
    const oldDebugHashes = [
      'HCd12b98511010b60631a7ef41e6e674f0',
      'HCd12b98511010b60631a7ef41e6e674f1',
      'HCd12b98511010b60631a7ef41e6e674f2',
      'HCd12b98511010b60631a7ef41e6e674f3'
    ];
    if (this.data.hash && !oldDebugHashes.includes(this.data.hash)) {
      return
    }
    this.data.hash = '';
    for (let i = 0; i < 8; i++) {
      this.data.hash += (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
    }
    this.data.hash = sdk.config.elite.prefix + this.data.hash;
  }

  percent() {
    if (this.data.percent) return;

    this.data.percent = Number(Date.now().toString().substr(-2, 2));
  }

  language() {
    this.data.language = (navigator && navigator.language) ? navigator.language.replace(/-.*/, '').toLowerCase() : '';
  }

  country() {
    return new Promise((resolve) => {
      if (this.data.country) return resolve(true);

      Network.get(sdk.config.countryCheckUrl).then((response) => {
        try {
          let json = JSON.parse(response);
          if (json && json.country_code) {
            this.data.country = json.country_code.toLowerCase();
            resolve(true);
          }
        } catch (e) {
          resolve(true);
        }
      }).catch(() => {
        resolve(true);
      });
    });
  }
}

export default User;
