var proto  = {};

proto.content  = `package proto.api.response;
message ResponseStatus {
	enum Errno {
		PLATFORM_NOT_SUPPORTED  = 10;
		OPERATION_NOT_SUPPORTED = 11;
		UNAUTHORIZED            = 12;
		INVALID_INPUT           = 13;
		SERVER_INTERNAL         = 14;
	}

	required bool   success       = 1;
	optional uint32 error_code    = 2;
	optional string error_message = 3;
	optional string debug         = 4;
}

message Package {
	enum Id {
		ELITE              = 1;
		DEDICATED          = 10;
		TURBO              = 11;
		TRIAL              = 15;
		ADS                = 13;
		VIRTUAL_LOCATION   = 12;
		FIVE_EXTRA_DEVICES = 18;
		TRIALWARE          = 19;
	}

	required Id      id              = 1;
	optional bool    is_active       = 2;
	optional uint64  expiration_date = 3;
	optional bool    is_lifetime     = 4;
}

message Timestamp {
  optional int64 seconds = 1;
  optional int32 nanos = 2;
}

message TimeRange {
	optional Timestamp start = 1;
	optional Timestamp end   = 2;
}

message UserStatus {
	repeated Package.Id packages_list = 1;
	repeated Package    packages      = 2;

	optional string login        = 3;
	optional uint32 devices_max  = 4;
	optional uint32 devices_used = 5;
	optional bool   is_anonymous = 6;
	optional TimeRange trial_period = 7;
}

message PaymentPopup {
	enum Type {
		POPUP = 1;
		PAGE  = 2;
	}

	required string url            = 1;
	required uint32 width          = 2;
	required uint32 height         = 3;
	required uint32 corners_radius = 4;
  required Type   type           = 5;
}

message Config {
	required ResponseStatus ResponseStatus    = 1;
	optional PaymentPopup   PaymentPopup      = 2;
	optional PaymentPopup   PaymentOptinTrial = 3;
}

message SD {
	required ResponseStatus ResponseStatus = 1;
  optional string raw_data = 2;
}

message Error {
	required ResponseStatus ResponseStatus = 1;
}

message RestorePassword {
	required ResponseStatus ResponseStatus = 1;
}

message Signin {
	required ResponseStatus ResponseStatus = 1;
	optional UserStatus     UserStatus     = 2;
	optional string         token          = 3;
}

message Signout {
	required ResponseStatus ResponseStatus = 1;
	optional UserStatus     UserStatus     = 2;
	optional string         token          = 3;
}

message Signup {
	required ResponseStatus ResponseStatus = 1;
	optional UserStatus     UserStatus     = 2;
	optional string         token          = 3;
}

message Status {
	required ResponseStatus ResponseStatus = 1;
	optional UserStatus     UserStatus     = 2;
	optional string         token          = 3;
	optional string         client_cache   = 4;
}

message BNLink {
    enum LinkStatus {
        ACCEPTED    = 202;
        NO_CONTENT  = 204;
        BAD_REQUEST = 400;
        CONFLICT    = 409;
        LOCKED      = 423;
    }

    required ResponseStatus ResponseStatus = 1;
    optional LinkStatus link_status = 2;
}`;

export default proto;