import Timer        from './Timer';
import Speed        from './Speed';
import Status       from './Status';
import Authorization from '../../shared/Proxy/Authorization';

import BypassWebsites from './BypassWebsites';
import SecuredWebsites from './SecuredWebsites';

const STORAGE_ERROR  = 'Storage.error';
const GDPR_CONNECTED = 'GDPR.Connected';

class Proxy extends Status{
  constructor() {
    super();

    new Authorization();

    sdk.proxy = {
      status:     this.status,
      render:     this.render.bind(this),
      connect:    this.connect.bind(this),
      reconnect:  this.reconnect.bind(this),
      disconnect: this.disconnect.bind(this),

      getServers: this.servers.config.getServers.bind(this.servers.config),
      getLocations: this.servers.config.getLocations.bind(this.servers.config),
      getGlobalStatus: this.getGlobalStatus.bind(this),
      getActiveServers: this.servers.getActive.bind(this.servers),
      disableOtherProxy: this.disableOtherProxy.bind(this),

      onProxyError: this.onProxyError,
      onStatusChanged: this.onStatusChanged,
      onGlobalStatusChanged: this.onGlobalStatusChanged,

      disableErrors: false
    };

    let timer       = new Timer();
    sdk.proxy.timer = timer.get.bind(timer);

    let bypass  = new BypassWebsites();
    sdk.proxy.bypass = {
      add: bypass.add.bind(bypass),
      get: bypass.get.bind(bypass),
      remove:   bypass.remove.bind(bypass),
      isInList: bypass.isInList.bind(bypass),
      onChange: bypass.onChange
    };

    this.SecuredWebsites();

    let speed = new Speed();
    sdk.proxy.speed = {
      get:  speed.get.bind(speed),
      ping: speed.ping.bind(speed),
      test: speed.test.bind(speed)
    };

    this.initDone();
  }

  SecuredWebsites(alexa) {
    let secured = new SecuredWebsites(alexa);
    sdk.proxy.secured = {
      add: secured.add.bind(secured),
      get: secured.get.bind(secured),
      remove:   secured.remove.bind(secured),
      isInList: secured.isInList.bind(secured),
      isLocked: secured.isLocked.bind(secured),
      unlock: secured.unlock.bind(secured),
      onChange: secured.onChange
    };
  }

  initDone() {
    if(sdk.storage.get(STORAGE_ERROR)) {
      this.renderClearProxy();
    }
    else {
      sdk.proxy.disconnect();
    }
  }

  connect(location, delay) {
    delay = delay ? delay : sdk.config.proxy.connecting.delay * 1000;
    location = location ? location : 'optimal';
    debug('Connect: '+location);

    return new Promise((resolve) => {
      sdk.storage.set(GDPR_CONNECTED, true).then(() => {
        this.updateStatus('connected', location).then(() => {
          this.updateProxy().then(() => {
            this.onStatusChanged.dispatch({status: 'connecting', location: this.status.location});

            setTimeout(() => {
              this.onStatusChanged.dispatch(this.status);
              resolve();
            }, delay);
          });
        });
      });
    });
  }

  reconnect(location) {
    location = location ? location : 'optimal';
    debug('Reconnect: '+location);

    return new Promise((resolve) => {
      this.updateStatus('connected', location).then(() => {
        this.updateProxy().then(() => {
          this.onStatusChanged.dispatch({status: 'connected', location: this.status.location});
          resolve();
        });
      });
    });
  }

  disconnect(delay) {
    delay = delay ? delay : sdk.config.proxy.disconnecting.delay * 1000;
    debug('Disconnect');

    return new Promise((resolve) => {

      this.updateStatus('disconnected').then(() => {
        this.updateProxy().then(() => {
          this.onStatusChanged.dispatch({status: 'disconnecting', location: this.status.location});

          setTimeout(() => {
            this.onStatusChanged.dispatch(this.status);
            resolve();
          }, delay);
        });
      });
    });
  }
}

export default Proxy;
