class HssConfig {
  static loadConfig() {
    return new Promise(resolve => {
      sdk.getSD().then((response) => {
        if(!response) {
          return;
        }

        let config = HssConfig.parseConfig(response);
        resolve([response, config]);
      });
    });
  }

  static parseConfig(data) {
    let config = {
      servers: {
        elite: {},
        regular: {}
      },
      locations: {
        elite: [],
        regular: []
      }
    };

    for(let c in data) {
      if(!data.hasOwnProperty(c)) continue;

      let cnt = c.toLowerCase();
      for(let k in data[c]) {
        if(!data[c].hasOwnProperty(k)) continue;
        data[c][k].address = data[c][k].host;
        data[c][k].scheme = "https";
        data[c][k].country = cnt;
      }

      config.locations.elite.push(cnt);
      config.servers.elite[cnt] = data[c];

      if(data[c].length > 0) {
        config.locations.regular.push(cnt);
        config.servers.regular[cnt] = data[c];
      }
    }

    return config;
  }
}

export default HssConfig;
