import ProtobufJS from 'protobufjs';
import protoRequest from './protoRequest';
import protoResponse from './protoResponse';

class Proto {
  constructor() {
    this.protoRequest  = ProtobufJS.loadProto(protoRequest.content).build('proto.api.request');
    this.protoResponse = ProtobufJS.loadProto(protoResponse.content).build('proto.api.response');
  }

  getBase64DeviceInfo(deviceInfo) {
    return new Promise((resolve, reject) => {
      let message = this.protoRequest.DeviceInfo;
      let base64  = new message(deviceInfo).encode().toBase64();
      base64 = ((base64.replace(/\+/gi, "-")).replace(/\//gi, "_")).replace(/=/gi, "");
      resolve(base64);
    });
  }

  request(messageName, urlPrefix, content) {
    return new Promise((resolve, reject) => {
      debug('Proto request: '+urlPrefix);
      debug(JSON.stringify(content));
      debug('-------------------------------------')
      let message = this.protoRequest[messageName];
      let buffer  = new message(content).encode().toArrayBuffer();

      this.sendRequest(messageName, urlPrefix, buffer).then((result) => {
        try {
          if(!result.ResponseStatus.success) return reject(result);
          return resolve(result);
        } catch (e) {
          return reject(result);
        }
      }).catch((status) => {
        let result = {
          ResponseStatus: {
            success: false,
            error_code: status,
            error_message: null
          }
        };

        reject(result);
      });
    });
  }

  response(messageName, response) {
    return new Promise((resolve, reject) => {
      let message = this.protoResponse[messageName];
      let content = message.decode(response);
      content     = this.transformToObject(content);

      debug('Proto response:');
      debug(JSON.stringify(content));
      debug('-------------------------------------')

      resolve(content);
    });
  }

  sendRequest(messageName, urlPrefix, buffer) {
    return new Promise((resolve, reject) => {
      let request = new XMLHttpRequest();
      request.open('POST', sdk.config.elite.api+urlPrefix);
      request.overrideMimeType('application/octet-stream');
      request.responseType = "arraybuffer";

      request.onload = function() {
        if (request.status >= 200 && request.status < 300 && request.response) {
          this.response(messageName, request.response).then((result) => {
            resolve(result);
          }).catch((e) => {
            reject(200);
          });
        }
        else {
          reject(request.status);
        }
      }.bind(this);

      request.onerror = function() {
        reject(request.status);
      }.bind(this);

      request.send(buffer);
    });
  }

  transformToObject(message) {
    let object = {};

    for(let k in message) {
      if(typeof (message[k]) === 'function') {
        continue;
      }

      if(message[k] === null) {
        object[k] = message[k];
      }
      else if(typeof (message[k]) === 'object') {
        object[k] = this.transformToObject(message[k]);
      }
      else {
        object[k] = message[k];
      }
    }

    return object;
  }
}

export default Proto;