import Proto from './proto/Proto';

const DATA = 'User.data';

class Elite {
  constructor() {
    this.popUp = {
      deviceInfo: '',
      trial: 'https://www.hsselite.com/payment/cc/week2month?chrome_ext=1',
      payment: 'https://www.hsselite.com/pre_purchase?chrome_ext=1'
    };

    this.proto = new Proto();
    sdk.getSD = this.getSD.bind(this);
  }

  init() {
    return new Promise(resolve => {
      this.getStatus().then(() => {
        this.getPopUp();
        this.showToPublic();
        return resolve();
      });
    });
  }

  getStatus() {
    return new Promise(resolve => {
      this.proto.request('Status', 'status', {DeviceInfo: this.deviceInfo()}).then((result) => {
        this.statusCallback(result).then(() => {
          return resolve();
        });
      }).catch((result) => {
        this.statusCallback(result).then(() => {
          return resolve();
        });
      });
    });
  }

  getSD() {
    return new Promise(resolve => {
      this.proto.request('SD', 'config/sd', {DeviceInfo: this.deviceInfo(), requested_country: "US"}).then((result) => {
        let serves;
        if(result && result.raw_data) {
          try {
            let tmp = JSON.parse(result.raw_data);
            if(tmp.servers) {
              serves = tmp.servers;
            }
          }catch (e) {}

          resolve(serves);
        }
      }).catch(() => {
        resolve(undefined);
      });
    });
  }

  signIn(login, password) {
    return new Promise((resolve, reject) => {
      let data = {
        category: 'signin'
      };

      this.eliteEvent(data);

      this.proto.request('Signin', 'signin', {DeviceInfo: this.deviceInfo(), login: login, password: password}).then((result) => {
        this.statusCallback(result).then((status) => {
          let data = {
            category: 'signedin',
            touchpoint: (sdk.user.status.elite?'elite':'free')

          };
          this.eliteEvent(data);

          if(sdk.user.status.elite) {
            sdk.forceLoadSD();
          }

          return resolve(status);
        });
      }).catch((e) => {
        return reject(e.ResponseStatus);
      });
    });
  }

  signUp(login, password) {
    return new Promise((resolve, reject) => {
      let data = {
        category: 'signup'
      };

      this.eliteEvent(data);

      this.proto.request('Signup', 'signup', {DeviceInfo: this.deviceInfo(), email: login, password: password}).then((result) => {
        this.signIn(login, password).then((data) => {
          return resolve(data);
        });
      }).catch((e) => {
        return reject(e.ResponseStatus);
      });
    });
  }

  restorePassword(login) {
    return new Promise((resolve, reject) => {
      let data = {
        category: 'restorepassword'
      };

      this.eliteEvent(data);

      this.proto.request('RestorePassword', 'restore', {DeviceInfo: this.deviceInfo(), email: login}).then((result) => {
        return resolve(result);
      }).catch((e) => {
        return reject(e.ResponseStatus);
      });
    });
  }

  getPopUp() {
    this.proto.request('Config', 'config/payment_popup', {DeviceInfo: this.deviceInfo()}).then((result) => {
      if(!result || !result.ResponseStatus || !result.ResponseStatus.success) return;

      if(result.PaymentPopup && result.PaymentPopup.url) this.popUp.payment         = result.PaymentPopup.url;
      if(result.PaymentOptinTrial && result.PaymentOptinTrial.url) this.popUp.trial = result.PaymentOptinTrial.url;

      this.proto.getBase64DeviceInfo(this.deviceInfo()).then((deviceInfo) => {
        this.popUp.deviceInfo = deviceInfo;
      });
    });
  }

  openPopUp(touchpoint, domain) {
    this.domain     = domain;
    this.touchpoint = touchpoint;

    let data = {
      category: 'openpopup',
      touchpoint: this.touchpoint
    };

    this.eliteEvent(data);

    let params = 'touchpoint='+touchpoint+'&domain='+this.domain+'&user_co='+sdk.user.country+(sdk.proxy.status.status==='connected'?'&proxy_co='+sdk.proxy.status.location:'');

    let url = this.popUp.payment
      + (this.popUp.payment.indexOf('?')!==-1?'&':'?')
      + 'pbdi='+this.popUp.deviceInfo
      + '&ct='+encodeURIComponent(params)
      + '&utm_source='+sdk.config.elite.popupUtm+'&utm_medium=Free&utm_campaign='+this.touchpoint;

    sdk.tabs.openPage(url);
  }

  openTrialPromo(touchpoint, domain) {
    this.domain     = domain;
    this.touchpoint = touchpoint;

    let data = {
      category: 'opentrialpromo',
      touchpoint: this.touchpoint
    };

    this.eliteEvent(data);

    let params = 'touchpoint='+touchpoint+'&domain='+this.domain+'&user_co='+sdk.user.country+(sdk.proxy.status.status==='connected'?'&proxy_co='+sdk.proxy.status.location:'');

    let url = this.popUp.trial
      + '&pbdi='+this.popUp.deviceInfo
      + '&ct='+encodeURIComponent(params)
      + '&utm_source='+sdk.config.elite.popupUtm+'&utm_medium=Free&utm_campaign='+this.touchpoint;

    sdk.tabs.openPage(url);
  }

  statusCallback(status) {
    return new Promise(resolve => {
      sdk.user.status.token = undefined;
      sdk.user.status.elite = false;
      sdk.user.status.login = undefined;
      sdk.user.status.bnLink = false;
      sdk.user.status.authorized = false;

      if(status.token) {
        sdk.user.status.token = status.token;
      }

      if(
        status.UserStatus
        && typeof (status.UserStatus.is_anonymous) !== 'undefined'
        && !status.UserStatus.is_anonymous
        && status.UserStatus.login
        && status.UserStatus.login !== '_AFFREE_') {
        sdk.user.status.login      = status.UserStatus.login;
        sdk.user.status.authorized = true;
        sdk.user.status.bnLink     = false;
      }

      if(status.UserStatus && status.UserStatus.packages) {
        for(let k in status.UserStatus.packages) {
          let p = status.UserStatus.packages[k];
          if(p.is_active) {
            sdk.user.status.elite = true;
            break;
          }
        }
      }

      sdk.storage.set(DATA, sdk.user).then(() => {
        return resolve(JSON.parse(JSON.stringify(sdk.user.status)));
      });
    });
  }

  showToPublic() {
    sdk.user.bnLink    = this.bnLink.bind(this);
    sdk.user.signUp    = this.signUp.bind(this);
    sdk.user.signIn    = this.signIn.bind(this);
    sdk.user.signOut   = this.signOut.bind(this);
    sdk.user.openPopUp = this.openPopUp.bind(this);
    sdk.user.getStatus = this.getStatus.bind(this);
    sdk.user.openTrialPromo  = this.openTrialPromo.bind(this);
    sdk.user.restorePassword = this.restorePassword.bind(this);
  }

  bnLink(email) {
    let data = {
      category: 'bnLink'
    };

    this.eliteEvent(data);
    sdk.user.status.login  = email;
    sdk.user.status.bnLink = true;
    // sdk.storage.set(STATUS, sdk.user.status);

    return this.proto.request('BNLink', 'bn/link', {DeviceInfo: this.deviceInfo(), email: email}).then((result) => {
      if(result && result.link_status && result.link_status === 202 && !this.bnLinkInterval) {
        this.monitoreBnLink();
      }

      return result;
    });
  }

  monitoreBnLink() {
    this.bnLinkInterval = setInterval(() => {
      this.getStatus().then(() => {
        if(sdk.user.status.authorized) {
          return clearInterval(this.bnLinkInterval);
        }
      });
    }, 10000);
  }

  signOut() {
    let data = {
      category: 'signout'
    };

    this.eliteEvent(data);

    return this.proto.request('Signout', 'signout', {DeviceInfo: this.deviceInfo()}).then((result) => {
      return this.statusCallback(result);
    });
  }

  deviceInfo() {
    let deviceInfo = {
      hash: sdk.user.hash,
      make: sdk.config.elite.deviceInfo.make,
      model: sdk.config.elite.deviceInfo.model,
      os_name: navigator.userAgent.match(/\(([^);]+)/i) ? navigator.userAgent.match(/\(([^);]+)/i)[1] : 'undefined',
      language: sdk.user.language,
      platform: sdk.config.elite.deviceInfo.platform,
      app_version: 2.0,
      package_name: sdk.config.elite.deviceInfo.package_name
    };

    if(sdk.user.status.token) {
      deviceInfo.token = sdk.user.status.token;
    }

    return deviceInfo;
  }

  eliteEvent(data) {
    return new Promise(resolve => {
      try {
        let status = sdk.proxy.status;

        let event = {
          event: 'elite',
          category: data.category,
          touchpoint: data.touchpoint,
          'connection-country': (status&&(status.status==='connected')) ? status.country : null
        };

        sdk.reporting.analytics('elite', data.category, data.touchpoint);
        sdk.reporting.internal(event).then(() => {
          return resolve();
        });
      } catch (e) {
        return resolve();
      }
    });
  }
}

export default Elite;
